<script setup>
import { ref, reactive } from "vue"
import moment from "moment"
import { indexGetRichtext, indexPostService } from "@/api/totalApi"

const FILE_UPLOAD_URL = import.meta.env.VITE_UPLOAD_BASEURL

const formRef = ref(null)
// 表单数据
const formData = reactive({
  renderType: 1,
  submitType: 1,
  methodtype: 1,
  cate_id: 0,
  type: undefined,
  condition: undefined,
  title: undefined,
  description: undefined,
  price: undefined,
  images: [],
  imagesRender: [],
  from: undefined,
  to: undefined,
  datetime: new Date().getTime(),
  fromText: undefined,
  toText: undefined,
  dateText: undefined,
  // TODO 数量暂时不要
  //quantity: undefined,
})

const showFrom = ref(true)
const richTextContent = ref("")
const agreeSetup = ref(0)

const showFromAddressPopup = ref(false)
const showToAddressPopup = ref(false)
const showFromTimePopup = ref(false)

const hkDistrict = {
  // 第一级：香港主要区域
  root: [
    { label: "Hong Kong Island", value: "Hong Kong Island" },
    { label: "Kowloon", value: "Kowloon" },
    { label: "New Territories", value: "New Territories" },
  ],
  // 香港岛地区
  "Hong Kong Island": [
    { label: "Central and Western", value: "Central and Western" },
    { label: "Eastern", value: "Eastern" },
    { label: "Southern", value: "Southern" },
    { label: "Wan Chai", value: "Wan Chai" },
  ],
  // 九龙地区
  Kowloon: [
    { label: "Kowloon City", value: "Kowloon City" },
    { label: "Kwun Tong", value: "Kwun Tong" },
    { label: "Sham Shui Po", value: "Sham Shui Po" },
    { label: "Wong Tai Sin", value: "Wong Tai Sin" },
    { label: "Yau Tsim Mong", value: "Yau Tsim Mong" },
  ],
  // 新界地区
  "New Territories": [
    { label: "Kwai Tsing", value: "Kwai Tsing" },
    { label: "North", value: "North" },
    { label: "Sai Kung", value: "Sai Kung" },
    { label: "Sha Tin", value: "Sha Tin" },
    { label: "Tai Po", value: "Tai Po" },
    { label: "Tsuen Wan", value: "Tsuen Wan" },
    { label: "Tuen Mun", value: "Tuen Mun" },
    { label: "Yuen Long", value: "Yuen Long" },
  ],
}

const addressList = ref([hkDistrict["root"], hkDistrict["Hong Kong Island"]])

const onChangeDistrict = (pickerView, value, columnIndex, resolve) => {
  const item = value[columnIndex]

  if (columnIndex === 0) {
    // 选择了区域（第一列），更新第二列数据
    pickerView.setColumnData(1, hkDistrict[item.value])
  }
  resolve()
}

const currentDate = ref(new Date().getTime())

const handleSubmitOffer = async () => {
  //if (!formData.type) {
  //  useToast("类型不能为空")
  //  return
  //}

  const params = {
    methodtype: 1,
  }

  // 服务
  if (formData.renderType == 1 || formData.renderType == 3) {
    params.cate_id = formData.cate_id
    params.condition = formData.condition
    params.title = formData.title
    params.description = formData.description
    params.price = formData.price
    params.images = formData.images.join(",")
    params.from = formData.fromText
    params.to = formData.toText
    params.datetime = formData.dateText

    const r = await indexPostService(params)

    // 显示协议同意页面
    if (r.code == 200) {
      showFrom.value = false
    }
  }
  // 商品
  else if (formData.renderType == 2 || formData.renderType == 4) {
    showFrom.value = false
  }
  console.log(params)
}

const handleSubmitRequest = () => {
  const params = {
    methodtype: 2,
  }
  showFrom.value = false
}

// ============================================== 页面数据加载 ==============================================
onLoad(async (options) => {
  console.log(options)
  // 确认表单类型
  formData.renderType = options.method
  formData.submitType = options.type

  // 确认分类 type
  formData.cate_id = options.id
  formData.type = decodeURIComponent(options.name)

  // 获取富文本
  const richtext = await indexGetRichtext({ type: 4 })
  richTextContent.value = richtext.data.content
})

const handleClickSelectPictures = () => {
  uni.chooseImage({
    count: 1,
    sizeType: ["original", "compressed"],
    sourceType: ["album", "camera"],
    success: (res) => {
      uni.uploadFile({
        url: FILE_UPLOAD_URL,
        filePath: res.tempFilePaths[0],
        name: "file",
        success: (res) => {
          console.log("上传结果: ", res)
          const result = JSON.parse(res.data)

          if (result.code != 200) {
            useToast("File upload failed.")
            return
          }
          const { url, fullurl } = result.data
          formData.images.push(url)
          formData.imagesRender.push(fullurl)
        },
      })
    },
  })
}

const handleConfirmFromAddress = () => {
  console.log(formData.from)

  if (!formData.from) {
    formData.from = ["Hong Kong Island", "Central and Western"]
  }

  formData.fromText = formData.from.join(" ")
  showFromAddressPopup.value = false
}

const handleConfirmToAddress = () => {
  if (!formData.to) {
    formData.to = ["Hong Kong Island", "Central and Western"]
  }

  formData.toText = formData.to.join(" ")
  showToAddressPopup.value = false
}

const handleConfirmDateTime = () => {
  formData.dateText = moment(formData.datetime).format("YYYY-MM-DD")
  showFromTimePopup.value = false
}

const handleAgreeRule = () => {
  agreeSetup.value = agreeSetup.value + 1

  if (agreeSetup.value == 2) {
    useSwitchTab("/pages/tracking/index")
  }
}
</script>

